import React from 'react';
import { FormField, NumberInput } from '@wix/design-system';
import { BaseInputProps } from './types';
import {
  useEntityPageContext,
  useSelector,
  useWixPatternsContainer,
} from '@wix/patterns';
import { useController } from '@wix/patterns/form';

export interface NumberProps extends BaseInputProps<number | null> {}

export const Number = (props: NumberProps) => {
  const { field, dataHook } = props;

  const { translate: t, ...container } = useWixPatternsContainer();
  useSelector(() => container.initTask.status);

  const pageState = useEntityPageContext();

  const numberRange = field.validation?.numberRange;
  const min = numberRange?.min;
  const max = numberRange?.max;

  const controller = useController({
    name: field?.id ?? '',
    control: pageState.form.control,
    defaultValue: (pageState.entity as any)?.[field?.id ?? ''],
    rules: {
      validate: (value) => {
        if (
          field.validation?.required &&
          (value === null || value === undefined || value === '')
        ) {
          return t('cairo.fieldValidation.requiredField');
        }

        if (value !== null && value !== undefined) {
          if (min !== undefined && value < min) {
            return t('cairo.fieldValidation.minNumber', {
              minAmount: min,
            });
          }
          if (max !== undefined && value > max) {
            return t('cairo.fieldValidation.maxNumber', {
              maxAmount: max,
            });
          }
        }

        return true;
      },
    },
  });

  return (
    <FormField
      label={field.displayName}
      required={field.validation?.required}
      status={controller.fieldState.error ? 'error' : undefined}
      statusMessage={controller.fieldState.error?.message}
      dataHook={dataHook}
    >
      <NumberInput
        value={controller.field.value ?? undefined}
        onChange={(e) => controller.field.onChange(e)}
        onBlur={controller.field.onBlur}
        inputRef={controller.field.ref}
        dataHook={`number-${field.id}`}
      />
    </FormField>
  );
};
